angular.module('app').component('userDetails', {
	template: require('./user-details.component.html'),
	bindings: {
		allUsers: '<'
	},
	controller: function($routeParams) {
		this.user = this.allUsers.find((user) => user.id === parseInt($routeParams.id));
	},
});



